*, .container-fluid, .col, .row, nav, img, ul, li, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none; }

html, body {
  height: 100%;
  margin: 0; }

a, p {
  color: inherit; }

a:hover, a:active, a:focus {
  color: inherit;
  text-decoration: none; }

button {
  border: none;
  padding: 0 5px;
  margin: 0 5px; }
  button:hover {
    cursor: pointer; }
  button:focus {
    outline: none; }

input:enabled {
  border: none;
  outline: none; }

a:active, a:hover {
  outline: 0;
  text-decoration: none; }

a:active, a:focus {
  outline: 0;
  border: none; }

.center-all-flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

.flex-row {
  -ms-flex-direction: row;
      flex-direction: row; }

.flex-column {
  -ms-flex-direction: column;
      flex-direction: column; }



